import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HarvestLandRepositoryProvider } from '../../providers/harvest-land-repository/harvest-land-repository';

/**
 * Generated class for the HarvestPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-harvest',
  templateUrl: 'harvest.html',
})
export class HarvestPage {
    harvestLand;
    harvestLandID;
  constructor(public navCtrl: NavController, public navParams: NavParams, public harvestLandRepository:HarvestLandRepositoryProvider) {
    this.harvestLandID = this.navParams.get('harvestLandID');
    this.harvestLand = this.harvestLandRepository.getHarvestLand(this.harvestLandID)
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HarvestPage');
  }

}



