import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {UserPage} from '../user/user'
import { HarvestPage } from '../harvest/harvest';
import { Product } from '../../Models/Product';

/**
 * Generated class for the ProductPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-product',
  templateUrl: 'product.html',
})
export class ProductPage {

  product:Product;
  quantity: number;
  byQuantity: boolean = false;
  byWeight: boolean = false;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    
    this.product = this.navParams.get('product');
    if(this.product.pricingOption == 'q'){
      this.byQuantity = true;
    }else {
      this.byWeight = true;
    }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ProductPage');
  }

  
  getHarvestInfo(product){
    console.log(this.product.harvestLandID);
    this.navCtrl.push(HarvestPage, this.product.harvestLandID);
  }

  addToCart(product){
    //add to cart
  }

  goToSeller(sellerID){
    this.navCtrl.push(UserPage, 
      {user: {
        firstName : 'Fernando',
        lastName : 'Ortiz',
        userID : 'Forzzark',
        pictureURI: 'sdsdfg'

      }}
  );

  }

}



